import React, { Component } from 'react'
import { Form } from 'react-bootstrap';
import DatePicker from "react-datepicker";
export class AddDataPage extends Component {
  render() {
    return (
      <div>
        <div className="page-header">
          <h3 className="page-title"> Add Data </h3>
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item"><a href="!#" onClick={event => event.preventDefault()}>Dashboard</a></li>
              <li className="breadcrumb-item active" aria-current="page">Add Data</li>
            </ol>
          </nav>
        </div>
        <div className="col-md-12 grid-margin stretch-card">
          <div className="card">
            <div className="card-body">
              <h4 className="card-title">New data entry</h4>

              <form className="forms-sample">
              <Form.Group>
                <label htmlFor="date">Date</label><br></br>
                <DatePicker className="form-control w-100" id="date" size="lg" selected={new Date()}/>
              </Form.Group>
              <Form.Group>
                <label htmlFor="exampleInputUsername1">Well</label>
                <Form.Control type="text" id="exampleInputUsername1" placeholder="Well" size="lg" />
              </Form.Group>
              <Form.Group>
                <label htmlFor="exampleInputEmail1">Hourly Production</label>
                <Form.Control type="number" className="form-control" id="exampleInputEmail1" placeholder="Hourly production" />
              </Form.Group>
              <Form.Group>
                <label htmlFor="exampleInputPassword1">Hours operated</label>
                <Form.Control type="number" step="0.01" className="form-control" id="exampleInputPassword1" placeholder="Hours Operated" />
              </Form.Group>

              <button type="submit" className="btn btn-primary mr-2">Submit</button>
              <button className="btn btn-light">Cancel</button>
              </form>
          </div>
        </div>
      </div>
      </div >
    )
  }
}

export default AddDataPage
